.roomSwiper {
  border-radius: 12px;
  overflow: hidden;
}

.roomSwiper .swiper-button-prev::after,
.roomSwiper .swiper-button-next::after {
  font-size: 12px;
  font-weight: 700;
  color: #000;
}

.roomSwiper .swiper-slide {
  padding: 0;
  width: 100%;
  height: 280px;
  border-radius: 12px;
  overflow: hidden;
}

.roomSwiper .swiper-slide img {
  height: 100%;
  border-radius: 12px;
  overflow: hidden;
}

.roomSwiper .swiper-button-prev {
  border-radius: 100%;
  left: 15px;
  width: 25px;
  height: 25px;
  background-color: #fff;
  box-shadow: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s linear, opacity 0.3s linear;
}

.roomSwiper .swiper-button-next {
  border-radius: 100%;
  right: 15px;
  width: 25px;
  height: 25px;
  background-color: #fff;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s linear, opacity 0.3s linear;
}

.roomLink:hover .roomSwiper .swiper-button-prev,
.roomLink:hover .roomSwiper .swiper-button-next {
  visibility: visible;
  opacity: 1;
}
