.mySwiper {
  margin: 0 40px !important;
}

.mySwiper .swiper-slide:hover {
  border-bottom: gray 2px solid;
}
.mySwiper .swiper-slide:hover {
  border-bottom: gray 2px solid;
}
.mySwiper .swiper-button-prev {
  border-radius: 100%;
  border: gray 1px solid;
  left: 0px;
  width: 25px;
  height: 25px;
}

.mySwiper .swiper-button-next {
  border-radius: 100%;
  border: gray 1px solid;
  right: 0px;
  width: 25px;
  height: 25px;
}

.mySwiper .swiper-button-prev::after,
.mySwiper .swiper-button-next::after {
  font-size: 12px;
  font-weight: 700;
  color: #000;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  padding: 10px;
  color: rgb(143, 137, 137);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-bottom: 2px transparent solid;
  transition: all 0.3s;
  cursor: pointer;
}

.swiper-slide img {
  display: block;
  object-fit: cover;
}

.swiper-slide span {
  font-size: 12px;
  margin-top: 10px;
}

